* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer, .board-container {
  justify-content: flex-start;
  align-items: center;
  gap: 15px;
  display: flex;
}

.board-container {
  padding-bottom: 10px;
}

#datetime-picker {
  min-width: 300px;
  min-height: 50px;
  color: teal;
  border: 2px solid teal;
  border-radius: 4px;
  font-size: 20px;
  font-weight: bolder;
}

#datetime-picker:focus {
  outline: 1px solid tomato;
}

button[data-start] {
  min-width: 150px;
  min-height: 50px;
  color: teal;
  background-color: gold;
  border: 2px solid teal;
  border-radius: 4px;
  font-size: 20px;
  font-weight: bolder;
}

button[data-start]:hover, button[data-start]:focus {
  color: gold;
  background-color: teal;
  border: 2px solid gold;
}

button[data-start]:disabled {
  background-color: tomato;
}

.field span {
  min-width: 100px;
  text-align: center;
  color: teal;
  margin: 5px auto;
  font-size: 18px;
  font-weight: bolder;
  display: block;
}

.field .value {
  font-size: 32px;
}

